import React from "react";
import * as XLSX from "xlsx";

export const Winners = (props: {names: string[], reset(): void}) => {
    const onDownload = () => {
        const wb = XLSX.utils.book_new();
        const ws = XLSX.utils.json_to_sheet(props.names.map((name) => ({firstName: name.split(' ')[0], lastName: name.split(' ')[1]})));
        XLSX.utils.book_append_sheet(wb, ws, "Winners");
        XLSX.writeFile(wb, "winners.xlsx");
    }
    return (
        <div className="rounded overflow-hidden shadow-lg p-4">
            <h3 className="text-lg font-bold my-4">Zwycizcy</h3>
            <div className="flex items-center justify-between border-collapse my-4">
                <button onClick={props.reset} className="w-full border-2 rounded-r-none text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">
                    Zresetuj
                </button>
                <button onClick={onDownload} className="w-full border-2 rounded-l-none text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">
                    Pobierz
                </button>
            </div>
            <ul className="list-decimal px-6">
            {props.names.map((name, index) => (
                <li key={index} className="py-2">{name}</li>
            ))}
            </ul>
        </div>
    );
}
